.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
  
}


#inputText {
    border: 2px solid #ccc;
    width: 300px;
    height: 50px;
    border-radius: 5px;
    text-align: center;
    margin-bottom: 10px;
    
}


#addButton, #editButton, #delButton {
    width: 80px;
    height: 30px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-right: 5px;
}

#addButton {
    background-color: #4caf50;
    color: white;
}

#addButton:hover {
    background-color: #45a049;
}

#editButton {
    background-color: #fdd835;
    color: black;
}

#editButton:hover {
    background-color: #fbc02d;
}

#delButton {
    background-color: #f44336;
    color: white;
}

#delButton:hover {
    background-color: #d32f2f;
}
#handleView {
    background-color: skyblue;
    color: black;
    width: 80px;
    height: 30px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-right: 5px;
}

#handleView:hover {
    background-color: black;
    color: white;
}
#listElement {
    list-style-type: none;
    border: 2px solid #2196f3;
    border-radius: 5px;
    padding: 10px;
    text-align: center;
    margin-bottom: 10px;
    width: 280px;
    background-color: #fff;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: black;
}
#Navbar{
    position: relative;
    list-style-type: none;
    font-weight: bolder;
    font-size: 25px;
    border: dashed 2px black;
    border-radius: 25px;
    width: 100%;
    max-width: 300px;
    padding-left: 2%;
    animation-name: NavRotate;
    animation-duration: 5s;
    animation-iteration-count: infinite;
    text-shadow: 1px 1px 1px white;
    word-wrap: break-word;
}
@keyframes NavRotate {
    0%{
        border-color: red;
    }
    20%{
        border-color: blue;
    
    }
    40%{
        border-color: green;
      
    }
    60%{
        border-color: yellow;
      
       
    }
    80%{
        border-color: purple;
     
    }
    100%{
        border-color: pink;

      
    }
}

#headTitle{
    color: green;
    animation-name: Typing;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    overflow: hidden;
    white-space: nowrap;
}
@keyframes Typing {
    from{
        width: 0%;
    }
    to{
        width: 100%;

    }
    
}
.Footer{
    display: flex;
    justify-content: center;
}
.Footer section{
    display: flex;
}
#linkedLogo{
    width:50px;
    height: 50px;
    margin-left: 20px;
    cursor: pointer;
    padding-top: 2%;

}
.Footer section a{
    padding-top: 2%;
}
#addButton{
    animation-name: ButtonAnime;
    animation-duration: 1s;
    animation-iteration-count: 1;
}
@keyframes ButtonAnime {
    from{
        transform: scale(50%);
    }
    to{
        transform: scale(100%);
    }
}

